import React from 'react';
import {
  TextField,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  FormControlLabel,
  Checkbox,
  Button,
  CircularProgress
} from '@mui/material';

function Row(props) {
  return (
    <div className="row">
      <div className={'col-12 ' + props.className + ' py-2'}>{props.children}</div>
    </div>
  );
}

export function CreateProduct(props) {
  const [formData, setFormData] = React.useState(
    !props.product
      ? {
          Name: '',
          Type: 0,
          SKU: '',
          Description: '',
          Price: 0,
          PaymentType: 1, // 1 -> Coin, 3 -> IAB
          Category: 0,
          EnabledFor: 2, // 0 -> all, 1 -> VIP , 2 -> Disabled
          Balance: 0, // For coin only , the coin amount that user gets
          Item: 'PrimaryCoin', // Should always be 'PrimaryCoin'
          Offer: 0, // -- Not now
          Booster: 0, // booster only , Booster amount
          ActiveForVip: false, // For vip person while its vip
          VipGift: false, //  Gift for vip person
          ExpireDate: 0, // Expire time null or to seconds
          IsUnique: false, //
          IsUniqueType: false, //
          Rarity: 1, // 1 Super Legened , 2 Legend, 3 super rare, 4 rare , 5 common
          hasExpireDate: false
        }
      : { ...props.product, hasExpireDate: false }
  );
  React.useEffect(() => {
    if (props.product) {
      if (props.product.ExpireDate) {
        setFormData({
          ...formData,
          hasExpireDate: true
        });
      }
    }
  }, [0]);
  const [loading, setLoading] = React.useState(null);

  const OnChangeHandler = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };
  const CheckboxOnChangeHandler = (e, v) => {
    setFormData({
      ...formData,
      [e.target.name]: v
    });
  };

  const Controller = (n) => ({
    value: formData[n],
    checked: formData[n],
    onChange: OnChangeHandler,
    name: n
  });
  return (
    <div className="row">
      {loading === null ? (
        <div className="col-12 p-4">
          <Row>
            <TextField
              label="نام"
              {...Controller('Name')}
              variant="outlined"
              className="w-100 shadow"
            />
          </Row>
          <Row>
            <TextField
              label="توضیحات"
              {...Controller('Description')}
              multiline
              rows={5}
              variant="outlined"
              className="w-100 shadow"
            />
          </Row>
          <Row>
            <FormControl fullWidth>
              <InputLabel id="type-id">نحوه پرداخت</InputLabel>
              <Select
                labelId="type-id"
                {...Controller('PaymentType')}
                label="نحوه پرداخت"
                variant="outlined"
                className="shadow">
                <MenuItem value={1}> سکه </MenuItem>
                <MenuItem value={3}> درون برنامه ای </MenuItem>
              </Select>
            </FormControl>
          </Row>
          <Row>
            <FormControl fullWidth>
              <InputLabel id="type-id">کم یابی</InputLabel>
              <Select
                labelId="type-id"
                {...Controller('Rarity')}
                label="کم یابی"
                variant="outlined"
                className="shadow">
                <MenuItem value={1}> خیلی نادر </MenuItem>
                <MenuItem value={2}> نادر </MenuItem>
                <MenuItem value={3}> خیلی کمیاب </MenuItem>
                <MenuItem value={4}> کمیاب </MenuItem>
                <MenuItem value={5}> رایج </MenuItem>
              </Select>
            </FormControl>
          </Row>
          <Row>
            <TextField
              disabled={formData['PaymentType'] === 1}
              label="شناسه محصول در بازار و مایکت"
              {...Controller('SKU')}
              variant="outlined"
              className="w-100 shadow"
            />
          </Row>
          <Row>
            <FormControl fullWidth>
              <InputLabel id="type-id">دسته بندی</InputLabel>
              <Select
                labelId="type-id"
                label="دسته بندی"
                variant="outlined"
                className="shadow"
                {...Controller('Category')}>
                <MenuItem value={0}> سکه </MenuItem>
                <MenuItem value={1}> بوستر </MenuItem>
                <MenuItem value={2}> وی آی پی </MenuItem>
                <MenuItem value={3}> کاراکتر </MenuItem>
                <MenuItem value={4}> آواتار </MenuItem>
                <MenuItem value={5}> محیط </MenuItem>
              </Select>
            </FormControl>
          </Row>
          <Row>
            <TextField
              label="قیمت"
              {...Controller('Price')}
              type={'number'}
              variant="outlined"
              className="w-100 shadow"
            />
          </Row>
          <Row>
            <FormControl fullWidth>
              <InputLabel id="enable-for-id">نمایش برای</InputLabel>
              <Select
                labelId="enable-for-id"
                label="نمایش برای"
                disabled={!props.product}
                variant="outlined"
                {...Controller('EnabledFor')}
                className="shadow">
                <MenuItem value={0}> همه </MenuItem>
                <MenuItem value={1}> وی آی پی </MenuItem>
                <MenuItem value={2}> هیچکس </MenuItem>
              </Select>
            </FormControl>
          </Row>
          {formData.Category === 0 && (
            <Row>
              <TextField
                label="بالانس"
                {...Controller('Balance')}
                type={'number'}
                variant="outlined"
                className="w-100 shadow"
              />
            </Row>
          )}
          {formData.Category === 1 && (
            <Row>
              <TextField
                label="ضریب"
                {...Controller('Booster')}
                type={'number'}
                variant="outlined"
                className="w-100 shadow"
              />
            </Row>
          )}
          <Row>
            <FormControlLabel
              control={
                <Checkbox {...Controller('hasExpireDate')} onChange={CheckboxOnChangeHandler} />
              }
              label={'تاریخ انقضا دارد ؟'}
            />
            {formData.hasExpireDate && (
              <TextField
                label="بعد از چند روز منقضی شود ؟"
                {...Controller('ExpireDate')}
                type={'number'}
                variant="outlined"
                className="w-100 shadow"
              />
            )}
          </Row>
          <Row>
            <FormControl fullWidth>
              <FormControlLabel
                disabled={formData.VipGift}
                control={
                  <Checkbox {...Controller('ActiveForVip')} onChange={CheckboxOnChangeHandler} />
                }
                label={'فعال برای افراد vip'}
              />
              <FormControlLabel
                disabled={formData.ActiveForVip}
                control={<Checkbox {...Controller('VipGift')} onChange={CheckboxOnChangeHandler} />}
                label={'هدیه به افراد vip'}
              />
              <FormControlLabel
                control={
                  <Checkbox {...Controller('IsUnique')} onChange={CheckboxOnChangeHandler} />
                }
                label={'از این محصول فقط یک بار میتواند بخرد'}
              />
              <FormControlLabel
                control={
                  <Checkbox {...Controller('IsUniqueType')} onChange={CheckboxOnChangeHandler} />
                }
                label={'از دسته بندی این محصول فقط یکبار میتواند بخرد'}
              />
            </FormControl>
          </Row>
          <Row>
            <Button
              onClick={() => {
                setLoading(
                  <Row className="text-center">
                    <CircularProgress />
                  </Row>
                );
                const data = formData;
                if (!data.hasExpireDate) data.ExpireDate = null;
                props
                  .onSubmit(data)
                  .then(() => {
                    alert('عملیات با موفقیت انجام شد');
                  })
                  .catch((e) => {
                    alert('عملیات با خطا مواجه شد');
                    console.log(e);
                  })
                  .finally(() => {
                    setLoading(null);
                  });
              }}>
              {!props.product ? 'ثبت' : 'آپدیت'}
            </Button>
          </Row>
        </div>
      ) : (
        <div className="col-12 p-5 text-center"> {loading} </div>
      )}
    </div>
  );
}
export default CreateProduct;
